@import url(https://use.typekit.net/glp0pxk.css);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

html{padding:0}body{padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#43C6AC;background:linear-gradient(to bottom, #1a1655, #43C6AC);background-size:cover}#root{padding:0;margin:0}.App{margin:0;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}nav{height:50px;padding:2vmin 0;font-family:pauline, sans-serif;font-style:normal;font-size:calc(32px + (64 - 32) * ((100vw - 300px) / (1800 - 300)));line-height:calc(36px + (68 - 36) * ((100vw - 300px) / (1800 - 300)));font-weight:700;text-align:center;color:#fff;background:#143961;background-size:cover}form{display:flex;flex-direction:row;justify-content:center;align-items:center;margin:2vmin 0}form input{margin:1vmin 2vmin;font-size:calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));text-align:center}form input[type='text']{margin:6px 0;padding:1vmin 4vmin;box-sizing:border-box;color:#fff;font-weight:600;background:rgba(25,25,25,0.9);border:1px solid white;border-radius:5px}form input[type='text']::-webkit-input-placeholder{color:white;opacity:0.8}form input[type='text']:-ms-input-placeholder{color:white;opacity:0.8}form input[type='text']::-ms-input-placeholder{color:white;opacity:0.8}form input[type='text']::placeholder{color:white;opacity:0.8}form input[type='submit']{display:block;padding:1vmin 4vmin;font-weight:600;color:#18473E;background:#85D0C1;border:1px solid white;border-radius:5px;transition:0.4s}form input[type='submit']:hover{color:#fff;background:#18473E;border:2px solid #fff;transition:0.4s}form select{margin:1vmin 2vmin}.content{display:flex;justify-content:center;align-content:center;flex-wrap:wrap}#search-word{padding:2vmin 0;font-size:calc(24px + (36 - 24) * ((100vw - 300px) / (1800 - 300)));line-height:calc(24px + (36 - 24) * ((100vw - 300px) / (1800 - 300)));font-weight:600;text-align:center;color:#fff;border-top:1px solid #fff}.card{min-width:150px;margin:1vmin;padding:1vmin 1vmin;border-radius:10px;border:1px solid #fff}.card h3{margin:0 0 1vmin 0;font-size:calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(24px + (28 - 24) * ((100vw - 300px) / (1800 - 300)));font-weight:600}.card p{font-size:calc(16px + (18 - 16) * ((100vw - 300px) / (1800 - 300)));line-height:calc(20px + (24 - 20) * ((100vw - 300px) / (1800 - 300)))}h3{text-align:center}p{text-align:center}#yellow-text{color:#ffffaf;font-weight:700}#status-message{height:100vh;color:lime}@media only screen and (max-width: 720px){form{display:flex;flex-direction:column;margin:4vmin 0}form input{margin:4vmin 0vmin}nav{height:40px;padding:4vmin 0}#search-word{padding:4vmin 0}}

